import ProjectTitle from '../../../components/school-training/ProjectTitle.vue'
import TaskList from '../../../components/school-training/TaskList.vue'
export default {
  name: 'app',
  components: {
    Header,
    TaskList,
    ProjectTitle
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      baseInfo: {},
      taskData: [{
        id: 1,
        name: '任务前测评',
        time: '2018.12.19',
        children: [{
          id: 1,
          name: '功能需求理解',
          content: '针对给出的业务功能模块功能特性，理解模块的功能需求和非功能需求（质量属性和设计约束）。'
        }]
      }, {
