        }]
      }, {
        id: 3,
        name: '需求分析',
        time: '2018.12.19',
        children: [{
          id: 1,
          name: '功能需求理解',
          content: '针对给出的业务功能模块功能特性，理解模块的功能需求和非功能需求（质量属性和设计约束）。'
        }]
      }]
    }
  },
  methods: {
    loadData () {
      let me = this
      let projectId = localStorage.getItem('projectId')
      this.$http.post('/teambuild/queryprojectinfo', {
        projectId: projectId
      }, res => {
        if (res.success) {
          me.baseInfo = res.data
        }
      })
      this.$http.post('/teambuild/querystagetasklist', {
        projectId: projectId
      }, res => {
        if (res.success) {
          me.taskData = res.data
        }
      })
    },
    beginProject () {
      window.location = 'team-search.html'
    }
  }
}
</script>

