<template>
  <div class="task-list">
    <div class="title">
      <span>任务列表：</span>
    </div>
    <div v-if="data">
      <div v-for="obj in data" v-bind:key="obj.stageId">
        <div class="item" :class="[obj.stageId == currentId ? 'active' : '']" @click="activeItem(obj.stageId)">
          <i v-if="obj.stageId == currentId" class="el-icon-caret-bottom"></i>
          <i v-else class="el-icon-caret-right"></i>
          <span :style="{fontWeight:(obj.stageId == currentId ? 'bold' : 400), color:(obj.stageId == currentId ? '#333333' : '#666666')}">{{obj.stageName}}</span>
          <span class="pull-right">
          <i class="iconfont iconshijian"></i>
          <span>至{{obj.stageEndTime}}</span>
        </span>
        </div>
        <div class="itemContent" v-if="obj.stageId == currentId">
          <div v-for="child in obj.stepList" v-bind:key="child.stepId" class="content">
            <div class="title">{{child.stepName}}</div>
            <div class="main">
              {{child.stepDescription}}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'TaskList',
  props: {
    activeId: Number,
    data: Array
  },
  data () {
    return {
      currentId: this.activeId
    }
  },
  methods: {
    activeItem (id) {
      if (this.currentId === id) {
        this.currentId = -1
      } else {
        this.currentId = id
      }
    }
  }
}
</script>
